.get-link input {
  width: 100%;
  height: 48px;
  border-radius: 5px;
  border: none;
  padding: 6px 16px;

  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 36px;
  letter-spacing: 0.12px;
  color: #34313d;
}

.get-link input:focus {
  outline: 3px solid #2bd0d0;
}

.get-link input::placeholder {
  font-family: "Poppins";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 36px;
  letter-spacing: 0.12px;
  color: #34313d;
  mix-blend-mode: normal;
  opacity: 0.5;
}

.get-link .invalid {
  outline: 3px solid #f46363;
}

.get-link .invalid::placeholder {
  color: #f46363;
}

.get-link #invalid-msg {
  margin-top: 4px;
  font-family: "Poppins";
  font-style: italic;
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.0818182px;
  color: #f46363;
  display: none;
}

@media only screen and (min-width: 1440px) {
  .get-link input {
    width: 769px;
    height: 100%;

    font-size: 20px;
    letter-spacing: 0.15px;
  }

  .get-link input::placeholder {
    font-size: 20px;
    letter-spacing: 0.15px;
  }

  .get-link #invalid-msg {
    margin-top: 8px;
    height: 18px;
    font-family: "Poppins";
    font-style: italic;
    font-weight: 500;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 0.109091px;
  }
}
